import React from "react";
import { Route, Switch } from "react-router-dom";
import Header from "./Components/Header";

import Home from "./Pages/Home";
import Generate from "./Pages/Generate";
import TransactionX from "./Pages/TransactionX";
import TransactionB from "./Pages/TransactionB";
import TransactionE from "./Pages/TransactionE";

import "./styles.css";

export default function App() {
  return (
    <>
      <Header />
      <Switch>
        <Route path="/" exact component={Home} />
        <Route path="/generate" component={Generate} />
        <Route path="/xid/:id" component={TransactionX} />
        <Route path="/bid/:id" component={TransactionB} />
        <Route path="/eid/:id" component={TransactionE} />
      </Switch>
    </>
  );
}
